import React, { useState, useEffect, useCallback, useMemo, useRef } from 'react';
import { toast } from 'react-hot-toast';
import { adminAPI, categoriesAPI, uploadAPI } from '../utils/api';
import { Upload, X, Image as ImageIcon } from 'lucide-react';

// Individual form field components to prevent re-renders
const InputField = React.memo(({ label, type = 'text', value, onChange, placeholder, required = false }) => {
  const handleChange = useCallback((e) => {
    onChange(e.target.value);
  }, [onChange]);

  return (
    <div className="space-y-2">
      <label className="block text-sm font-semibold text-gray-700">
        {label} {required && <span className="text-red-500">*</span>}
      </label>
      <input
        type={type}
        value={value || ''}
        onChange={handleChange}
        placeholder={placeholder}
        className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-200 text-gray-900"
      />
    </div>
  );
});

const TextAreaField = React.memo(({ label, value, onChange, placeholder, rows = 4 }) => {
  const handleChange = useCallback((e) => {
    onChange(e.target.value);
  }, [onChange]);

  return (
    <div className="space-y-2">
      <label className="block text-sm font-semibold text-gray-700">{label}</label>
      <textarea
        value={value || ''}
        onChange={handleChange}
        placeholder={placeholder}
        rows={rows}
        className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-200 resize-none text-gray-900"
      />
    </div>
  );
});

const ImageUploadField = React.memo(({ label, value, onChange }) => {
  const [uploading, setUploading] = useState(false);
  const [preview, setPreview] = useState(value || '');
  const fileInputRef = useRef(null);

  useEffect(() => {
    setPreview(value || '');
  }, [value]);

  const handleFileSelect = useCallback(async (e) => {
    const file = e.target.files?.[0];
    if (!file) return;

    // Validate file type
    const allowedTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/gif', 'image/webp'];
    if (!allowedTypes.includes(file.type)) {
      toast.error('Invalid file type. Please upload a JPG, PNG, GIF, or WebP image.');
      return;
    }

    // Validate file size (max 20MB)
    if (file.size > 20 * 1024 * 1024) {
      toast.error('File size too large. Maximum size is 20MB.');
      return;
    }

    try {
      setUploading(true);
      const response = await uploadAPI.uploadImage(file);
      
      if (response.ok && response.url) {
        onChange(response.url);
        setPreview(response.url);
        toast.success('Image uploaded successfully');
      } else {
        toast.error(response.message || 'Failed to upload image');
      }
    } catch (error) {
      console.error('Upload error:', error);
      toast.error(error.response?.data?.message || 'Failed to upload image');
    } finally {
      setUploading(false);
      // Reset file input
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
    }
  }, [onChange]);

  const handleUrlChange = useCallback((e) => {
    const url = e.target.value;
    onChange(url);
    setPreview(url);
  }, [onChange]);

  const handleRemoveImage = useCallback(() => {
    onChange('');
    setPreview('');
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  }, [onChange]);

  return (
    <div className="space-y-2">
      <label className="block text-sm font-semibold text-gray-700">{label}</label>
      
      {/* Image Preview */}
      {preview && (
        <div className="relative w-full h-48 mb-3 border border-gray-300 rounded-xl overflow-hidden bg-gray-50">
          <img
            src={preview}
            alt="Category preview"
            className="w-full h-full object-contain"
            onError={(e) => {
              e.target.style.display = 'none';
              e.target.nextElementSibling.style.display = 'flex';
            }}
          />
          <div className="hidden absolute inset-0 items-center justify-center bg-gray-100">
            <div className="text-center text-gray-500">
              <ImageIcon className="w-12 h-12 mx-auto mb-2 opacity-50" />
              <p className="text-sm">Failed to load image</p>
            </div>
          </div>
          <button
            type="button"
            onClick={handleRemoveImage}
            className="absolute top-2 right-2 p-1.5 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
            title="Remove image"
          >
            <X className="w-4 h-4" />
          </button>
        </div>
      )}

      {/* Upload Button */}
      <div className="flex items-center gap-3">
        <button
          type="button"
          onClick={() => fileInputRef.current?.click()}
          disabled={uploading}
          className="flex items-center gap-2 px-4 py-2.5 bg-blue-600 text-white rounded-xl hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed font-medium text-sm"
        >
          <Upload className="w-4 h-4" />
          {uploading ? 'Uploading...' : 'Upload Image'}
        </button>
        <input
          ref={fileInputRef}
          type="file"
          accept="image/jpeg,image/jpg,image/png,image/gif,image/webp"
          onChange={handleFileSelect}
          className="hidden"
        />
        <span className="text-sm text-gray-500">or enter URL</span>
      </div>

      {/* URL Input */}
      <input
        type="url"
        value={value || ''}
        onChange={handleUrlChange}
        placeholder="https://example.com/category.jpg"
        className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-200 text-gray-900"
      />
      <p className="text-xs text-gray-500">
        Upload an image file or paste an ImageBB/CDN URL. Supported formats: JPG, PNG, GIF, WebP (max 20MB)
      </p>
    </div>
  );
});

const SelectField = React.memo(({ label, value, onChange, options, required = false }) => {
  const handleChange = useCallback((e) => {
    onChange(e.target.value);
  }, [onChange]);

  return (
    <div className="space-y-2">
      <label className="block text-sm font-semibold text-gray-700">
        {label} {required && <span className="text-red-500">*</span>}
      </label>
      <select
        value={value || ''}
        onChange={handleChange}
        className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-200 text-gray-900"
      >
        {options.map(option => (
          <option key={option.value} value={option.value} className="text-gray-900">
            {option.label}
          </option>
        ))}
      </select>
    </div>
  );
});

const SaveButton = React.memo(({ onClick, disabled, loading = false, children }) => (
  <button
    onClick={onClick}
    disabled={disabled || loading}
    className={`px-8 py-3 rounded-xl font-semibold transition-all duration-200 ${
      disabled || loading
        ? 'bg-gray-300 text-gray-500 cursor-not-allowed'
        : 'bg-blue-600 text-white hover:bg-blue-700 shadow-lg hover:shadow-xl transform hover:scale-105'
    }`}
  >
    {loading ? 'Saving...' : children}
  </button>
));

const TabButton = React.memo(({ id, label, active, onClick }) => (
  <button
    onClick={() => onClick(id)}
    className={`px-6 py-3 rounded-xl font-semibold transition-all duration-200 ${
      active
        ? 'bg-blue-600 text-white shadow-lg'
        : 'bg-white text-gray-700 hover:bg-gray-50 border border-gray-200'
    }`}
  >
    {label}
  </button>
));

