import React, { useState, useEffect } from 'react';
import { adminAPI } from '../utils/api';

const AdminDashboard = () => {
  const [dashboardData, setDashboardData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      setLoading(true);
      const response = await adminAPI.getDashboard();
      if (response.ok) {
        setDashboardData(response.data);
      } else {
        setError(response.message || 'Failed to load dashboard data');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const StatCard = ({ title, value, icon, color, subtitle, trend }) => (
    <div className={`bg-white rounded-2xl p-8 shadow-lg border border-gray-100 hover:shadow-xl transition-all duration-300 transform hover:scale-105 ${color}`}>
      <div className="flex items-center justify-between">
        <div className="flex-1">
          <p className="text-gray-600 text-sm font-semibold mb-2 uppercase tracking-wide">{title}</p>
          <p className="text-4xl font-bold text-gray-900 mb-2">{value}</p>
          {subtitle && <p className="text-gray-500 text-sm leading-relaxed">{subtitle}</p>}
          {trend && (
            <div className="flex items-center mt-3">
              <span className={`text-sm font-semibold ${trend.positive ? 'text-green-600' : 'text-red-600'}`}>
                {trend.value}
              </span>
              <span className="text-gray-500 text-sm ml-2">vs last month</span>
            </div>
          )}
        </div>
        <div className="w-16 h-16 bg-blue-600 rounded-2xl flex items-center justify-center text-white shadow-lg">
          {icon}
        </div>
      </div>
    </div>
  );

  const SnapshotMetric = ({ label, value, helper }) => (
    <div className="bg-gray-50 rounded-2xl p-4 border border-gray-100 hover:bg-white hover:shadow-lg transition-all duration-200">
      <p className="text-sm text-gray-500 font-semibold uppercase tracking-wide mb-1">{label}</p>
      <p className="text-3xl font-extrabold text-gray-900">{value}</p>
      {helper && <p className="text-xs text-gray-500 mt-1">{helper}</p>}
    </div>
  );

  const ActivityItem = ({ activity }) => (
    <div className="flex items-center space-x-4 p-5 hover:bg-gray-50 rounded-xl transition-all duration-200 border border-transparent hover:border-gray-100">
      <div className="w-12 h-12 bg-blue-600 rounded-xl flex items-center justify-center text-white text-sm font-bold shadow-md">
        {activity.user_name?.charAt(0) || 'U'}
      </div>
      <div className="flex-1">
        <p className="text-gray-900 font-semibold text-base">{activity.action}</p>
        <p className="text-gray-500 text-sm mt-1">{activity.user_name}  {activity.timestamp}</p>
      </div>
      <span className={`px-3 py-1.5 rounded-full text-xs font-semibold ${
        activity.type === 'user' ? 'bg-blue-100 text-blue-800' :
        activity.type === 'listing' ? 'bg-green-100 text-green-800' :
        'bg-purple-100 text-purple-800'
      }`}>
        {activity.type}
      </span>
    </div>
  );

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-20 h-20 border-4 border-gray-200 border-t-blue-600 rounded-full animate-spin mx-auto mb-6"></div>
          <h2 className="text-2xl font-bold text-gray-900 mb-2">Loading Dashboard</h2>
          <p className="text-lg text-gray-600">Fetching your platform data...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center max-w-md">
          <div className="w-20 h-20 bg-red-100 rounded-2xl flex items-center justify-center mx-auto mb-6">
            <svg className="w-10 h-10 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
          </div>
          <h2 className="text-3xl font-bold text-gray-900 mb-3">Error Loading Dashboard</h2>
          <p className="text-gray-600 mb-8 text-lg leading-relaxed">{error}</p>
          <button 
            onClick={fetchDashboardData}
            className="bg-blue-600 text-white px-8 py-4 rounded-xl hover:bg-blue-700 transition-all duration-200 font-semibold shadow-lg hover:shadow-xl transform hover:scale-105"
          >
            Try Again
          </button>
        </div>
      </div>
    );
  }

  const data = dashboardData || {
    total_users: 0,
    users_by_type: { NORMAL: 0, INFLUENCER: 0, BRAND: 0 },
    pending_approvals: 0,
    active_subscriptions: 0,
    expired_subscriptions: 0,
    total_listings: 0,
    pending_listings: 0,
    revenue: { total: 0, subscriptions: 0, uploads: 0 },
    recent_activities: []
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="px-6 py-8">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-4xl font-bold text-gray-900 mb-2">Dashboard</h1>
              <p className="text-gray-600 text-lg">Welcome back! Here's what's happening with your platform.</p>
            </div>
            <div className="flex items-center space-x-4">
              <button 
                onClick={fetchDashboardData}
                className="bg-blue-600 text-white px-6 py-3 rounded-xl hover:bg-blue-700 transition-all duration-200 flex items-center space-x-3 shadow-lg hover:shadow-xl transform hover:scale-105"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                </svg>
                <span className="font-semibold">Refresh</span>
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="px-6 py-8">
        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <StatCard
            title="Total Users"
            value={data.total_users.toLocaleString()}
            subtitle={`${data.users_by_type.NORMAL} Normal, ${data.users_by_type.INFLUENCER} Influencers, ${data.users_by_type.BRAND} Brands`}
            icon={
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197m13.5-9a2.5 2.5 0 11-5 0 2.5 2.5 0 015 0z" />
              </svg>
            }
            color="bg-blue-50"
          />
          
          <StatCard
            title="Pending Approvals"
            value={data.pending_approvals}
            subtitle="Influencers & Brands awaiting review"
            icon={
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            }
            color="bg-yellow-50"
          />
          
          <StatCard
            title="Active Subscriptions"
            value={data.active_subscriptions}
            subtitle={`${data.expired_subscriptions} expired`}
            icon={
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 10V3L4 14h7v7l9-11h-7z" />
              </svg>
            }
            color="bg-green-50"
          />
          
          <StatCard
            title="Total Listings"
            value={data.total_listings.toLocaleString()}
            subtitle={`${data.pending_listings} under review`}
            icon={
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10" />
              </svg>
            }
            color="bg-purple-50"
          />
        </div>

        {/* Platform Snapshot */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 mb-8">
          <div className="lg:col-span-2 bg-white rounded-2xl p-8 shadow-lg border border-gray-100">
            <h3 className="text-2xl font-bold text-gray-900 mb-8">Platform Snapshot</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <SnapshotMetric
                  label="Normal Users"
                  value={data.users_by_type.NORMAL.toLocaleString()}
                  helper="Registered shoppers & viewers"
                />
                <SnapshotMetric
                  label="Influencers"
                  value={data.users_by_type.INFLUENCER.toLocaleString()}
                  helper="Creators approved on the platform"
                />
                <SnapshotMetric
                  label="Brands"
                  value={data.users_by_type.BRAND.toLocaleString()}
                  helper="Business accounts with listings"
                />
              </div>
              <div className="space-y-4">
                <SnapshotMetric
                  label="Pending Approvals"
                  value={data.pending_approvals.toLocaleString()}
                  helper="Provider applications awaiting review"
                />
                <SnapshotMetric
                  label="Active Subscriptions"
                  value={data.active_subscriptions.toLocaleString()}
                  helper={`${data.expired_subscriptions} expired`}
                />
                <SnapshotMetric
                  label="Listings In Review"
                  value={data.pending_listings.toLocaleString()}
                  helper={`${data.total_listings.toLocaleString()} total published listings`}
                />
              </div>
            </div>
          </div>

          <div className="bg-white rounded-2xl p-8 shadow-lg border border-gray-100">
            <h3 className="text-2xl font-bold text-gray-900 mb-8">Quick Actions</h3>
            <div className="space-y-4">
              <button className="w-full bg-blue-600 text-white py-3 px-6 rounded-xl hover:bg-blue-700 transition-all duration-200 text-left font-semibold shadow-md hover:shadow-lg transform hover:scale-105">
                Review Pending Approvals
              </button>
              <button className="w-full bg-green-600 text-white py-3 px-6 rounded-xl hover:bg-green-700 transition-all duration-200 text-left font-semibold shadow-md hover:shadow-lg transform hover:scale-105">
                Manage Subscriptions
              </button>
              <button className="w-full bg-purple-600 text-white py-3 px-6 rounded-xl hover:bg-purple-700 transition-all duration-200 text-left font-semibold shadow-md hover:shadow-lg transform hover:scale-105">
                View Analytics
              </button>
              <button className="w-full bg-yellow-600 text-white py-3 px-6 rounded-xl hover:bg-yellow-700 transition-all duration-200 text-left font-semibold shadow-md hover:shadow-lg transform hover:scale-105">
                System Settings
              </button>
            </div>
          </div>
        </div>

        {/* Recent Activity */}
        <div className="bg-white rounded-2xl p-8 shadow-lg border border-gray-100">
          <h3 className="text-2xl font-bold text-gray-900 mb-8">Recent Activity</h3>
          <div className="space-y-1">
            {data.recent_activities && data.recent_activities.length > 0 ? (
              data.recent_activities.map((activity, index) => (
                <ActivityItem key={index} activity={activity} />
              ))
            ) : (
              <div className="text-center py-12 text-gray-500">
                <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                  <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5H7a2 2 0 00-2 2v10a2 2 0 002 2h8a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
                  </svg>
                </div>
                <p className="text-lg font-medium text-gray-600">No recent activity to display</p>
                <p className="text-sm text-gray-500 mt-2">Activity will appear here as users interact with the platform</p>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboard;
