import React, { useState, useEffect } from 'react';
import { adminAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import { X, Crown, Calendar, Trash2 } from 'lucide-react';

const UserManagement = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [roleFilter, setRoleFilter] = useState('ALL');
  const [statusFilter, setStatusFilter] = useState('ALL');
  const [sortBy, setSortBy] = useState('created_at');
  const [sortOrder, setSortOrder] = useState('desc');
  const [selectedUser, setSelectedUser] = useState(null);
  const [showUserModal, setShowUserModal] = useState(false);
  const [actionLoading, setActionLoading] = useState(null);
  const [showSubscriptionModal, setShowSubscriptionModal] = useState(false);
  const [subscriptionPlans, setSubscriptionPlans] = useState([]);
  const [loadingPlans, setLoadingPlans] = useState(false);
  const [selectedPlanId, setSelectedPlanId] = useState(null);
  const [assigningSubscription, setAssigningSubscription] = useState(false);

  useEffect(() => {
    fetchUsers();
  }, [searchTerm, roleFilter, statusFilter, sortBy, sortOrder]);

  const fetchUsers = async () => {
    try {
      setLoading(true);
      const response = await adminAPI.getUsers({
        search: searchTerm,
        role: roleFilter,
        status: statusFilter,
        sort_by: sortBy,
        sort_order: sortOrder
      });
      
      if (response.ok) {
        setUsers(response.data.users || []);
      } else {
        setError(response.message || 'Failed to load users');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleUserAction = async (userId, action) => {
    try {
      setActionLoading(userId);
      const response = await adminAPI.updateUserStatus(userId, action);
      
      if (response.ok) {
        await fetchUsers();
        if (selectedUser && selectedUser.id === userId) {
          setSelectedUser({ ...selectedUser, status: action });
        }
        setError(''); // Clear any previous errors
      } else {
        // Handle specific error messages from backend
        if (response.status === 403) {
          setError('Cannot modify admin user status. Admin accounts are protected.');
        } else {
          setError(response.message || 'Failed to update user status');
        }
      }
    } catch (err) {
      // Handle network errors and other exceptions
      if (err.response && err.response.status === 403) {
        setError('Cannot modify admin user status. Admin accounts are protected.');
      } else if (err.response && err.response.data && err.response.data.message) {
        setError(err.response.data.message);
      } else {
        setError('Network error. Please try again.');
      }
    } finally {
      setActionLoading(null);
    }
  };

  const handleDeleteUser = async (userId) => {
    if (!window.confirm('Are you sure you want to delete this user? This action cannot be undone.')) {
      return;
    }

    try {
      setActionLoading(userId);
      const response = await adminAPI.deleteUser(userId);
      
      if (response.ok) {
        toast.success('User deleted successfully');
        await fetchUsers();
        setShowUserModal(false);
        setSelectedUser(null);
        setError(''); // Clear any previous errors
      } else {
        // Clear error state and show toast instead
        setError('');
        // Handle specific error messages from backend
        const errorMessage = response.message || 'Failed to delete user';
        if (response.status === 403 || errorMessage.includes('admin') || errorMessage.includes('protected')) {
          toast.error('Cannot delete admin users. Admin accounts are protected.');
        } else {
          toast.error(errorMessage);
        }
      }
    } catch (err) {
      // Clear error state and show toast instead
      setError('');
      // Handle network errors and other exceptions
      if (err.response && err.response.status === 403) {
        toast.error('Cannot delete admin users. Admin accounts are protected.');
      } else if (err.response && err.response.data && err.response.data.message) {
        const errorMessage = err.response.data.message;
        if (errorMessage.includes('admin') || errorMessage.includes('protected')) {
          toast.error('Cannot delete admin users. Admin accounts are protected.');
        } else {
          toast.error(errorMessage);
        }
      } else {
        toast.error('Network error. Please try again.');
      }
    } finally {
      setActionLoading(null);
    }
  };

  const openUserModal = async (user) => {
    try {
      const response = await adminAPI.getUserDetails(user.id);
      if (response.ok) {
        setSelectedUser(response.data);
        setShowUserModal(true);
      } else {
        setError(response.message || 'Failed to load user details');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    }
  };

  const loadSubscriptionPlans = async () => {
    try {
      setLoadingPlans(true);
      const response = await adminAPI.getSubscriptionPlans();
      if (response.ok) {
        setSubscriptionPlans(response.data?.plans || []);
      } else {
        toast.error('Failed to load subscription plans');
      }
    } catch (err) {
      toast.error('Failed to load subscription plans');
    } finally {
      setLoadingPlans(false);
    }
  };

  const openSubscriptionModal = async () => {
    setSelectedPlanId(null);
    await loadSubscriptionPlans();
    setShowSubscriptionModal(true);
  };

  const handleAssignSubscription = async () => {
    if (!selectedPlanId) {
      toast.error('Please select a subscription plan');
      return;
    }

    if (!selectedUser?.email) {
      toast.error('User email not found');
      return;
    }

    try {
      setAssigningSubscription(true);
      const response = await adminAPI.assignSubscription(selectedUser.email, selectedPlanId);
      
      if (response.ok) {
        toast.success('Subscription assigned successfully');
        setShowSubscriptionModal(false);
        // Refresh user details
        const userDetailsResponse = await adminAPI.getUserDetails(selectedUser.id);
        if (userDetailsResponse.ok) {
          setSelectedUser(userDetailsResponse.data);
        }
      } else {
        toast.error(response.message || 'Failed to assign subscription');
      }
    } catch (err) {
      toast.error(err.response?.data?.message || 'Failed to assign subscription');
    } finally {
      setAssigningSubscription(false);
    }
  };

  const handleRemoveSubscription = async () => {
    if (!selectedUser?.email) {
      toast.error('User email not found');
      return;
    }

    if (!selectedUser?.is_subscribed) {
      toast.error('User does not have an active subscription');
      return;
    }

    if (!window.confirm(`Are you sure you want to remove the subscription from ${selectedUser.email}?`)) {
      return;
    }

    try {
      setAssigningSubscription(true);
      const response = await adminAPI.removeSubscription(selectedUser.email);
      
      if (response.ok) {
        toast.success('Subscription removed successfully');
        // Refresh user details
        const userDetailsResponse = await adminAPI.getUserDetails(selectedUser.id);
        if (userDetailsResponse.ok) {
          setSelectedUser(userDetailsResponse.data);
        }
      } else {
        toast.error(response.message || 'Failed to remove subscription');
      }
    } catch (err) {
      toast.error(err.response?.data?.message || 'Failed to remove subscription');
    } finally {
      setAssigningSubscription(false);
    }
  };

  const getRoleBadgeColor = (role) => {
    switch (role) {
      case 'ADMIN': return 'bg-red-100 text-red-800';
      case 'INFLUENCER': return 'bg-purple-100 text-purple-800';
      case 'BRAND': return 'bg-blue-100 text-blue-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getStatusBadgeColor = (status) => {
    switch (status) {
      case 'ACTIVE': return 'bg-green-100 text-green-800';
      case 'SUSPENDED': return 'bg-yellow-100 text-yellow-800';
      case 'DELETED': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const filteredUsers = users.filter(user => {
    const matchesSearch = user.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         user.email?.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         user.phone?.includes(searchTerm);
    const matchesRole = roleFilter === 'ALL' || user.user_type === roleFilter;
    const matchesStatus = statusFilter === 'ALL' || user.status === statusFilter;
    
    return matchesSearch && matchesRole && matchesStatus;
  });

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-20 h-20 border-4 border-gray-200 border-t-blue-600 rounded-full animate-spin mx-auto mb-6"></div>
          <h2 className="text-2xl font-bold text-gray-900 mb-2">Loading Users</h2>
          <p className="text-lg text-gray-600">Fetching user data...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="px-6 py-8">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-4xl font-bold text-gray-900 mb-2">User Management</h1>
              <p className="text-gray-600 text-lg">Manage all registered users and their accounts</p>
            </div>
            <div className="flex items-center space-x-4">
              <button 
                onClick={fetchUsers}
                className="bg-blue-600 text-white px-6 py-3 rounded-xl hover:bg-blue-700 transition-all duration-200 flex items-center space-x-3 shadow-lg hover:shadow-xl transform hover:scale-105"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                </svg>
                <span className="font-semibold">Refresh</span>
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="px-6 py-8">
        {/* Filters and Search */}
        <div className="bg-white rounded-2xl p-6 shadow-lg border border-gray-100 mb-8">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
            {/* Search */}
            <div className="lg:col-span-2">
              <label className="block text-sm font-semibold text-gray-700 mb-2">Search Users</label>
              <div className="relative">
                <input
                  type="text"
                  placeholder="Search by name, email, or phone..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900"
                />
                <svg className="absolute left-3 top-3.5 w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                </svg>
              </div>
            </div>

            {/* Role Filter */}
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-2">Role</label>
              <select
                value={roleFilter}
                onChange={(e) => setRoleFilter(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900"
              >
                <option value="ALL">All Roles</option>
                <option value="NORMAL">Normal</option>
                <option value="INFLUENCER">Influencer</option>
                <option value="BRAND">Brand</option>
                <option value="ADMIN">Admin</option>
              </select>
            </div>

            {/* Status Filter */}
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-2">Status</label>
              <select
                value={statusFilter}
                onChange={(e) => setStatusFilter(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900"
              >
                <option value="ALL">All Status</option>
                <option value="ACTIVE">Active</option>
                <option value="SUSPENDED">Suspended</option>
                <option value="DELETED">Deleted</option>
              </select>
            </div>

            {/* Sort */}
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-2">Sort By</label>
              <select
                value={`${sortBy}-${sortOrder}`}
                onChange={(e) => {
                  const [field, order] = e.target.value.split('-');
                  setSortBy(field);
                  setSortOrder(order);
                }}
                className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900"
              >
                <option value="created_at-desc">Newest First</option>
                <option value="created_at-asc">Oldest First</option>
                <option value="name-asc">Name A-Z</option>
                <option value="name-desc">Name Z-A</option>
                <option value="email-asc">Email A-Z</option>
                <option value="email-desc">Email Z-A</option>
              </select>
            </div>
          </div>
        </div>

        {/* Error Message */}
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-xl p-4 mb-6">
            <div className="flex items-center">
              <svg className="w-5 h-5 text-red-600 mr-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              <p className="text-red-800 font-medium">{error}</p>
            </div>
          </div>
        )}

        {/* Users Table */}
        <div className="bg-white rounded-2xl shadow-lg border border-gray-100 overflow-hidden">
          <div className="px-6 py-4 border-b border-gray-200">
            <h3 className="text-xl font-bold text-gray-900">
              Users ({filteredUsers.length})
            </h3>
          </div>

          {filteredUsers.length === 0 ? (
            <div className="text-center py-12">
              <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197m13.5-9a2.5 2.5 0 11-5 0 2.5 2.5 0 015 0z" />
                </svg>
              </div>
              <p className="text-lg font-medium text-gray-600">No users found</p>
              <p className="text-sm text-gray-500 mt-2">Try adjusting your search or filter criteria</p>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">User</th>
                    <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Role</th>
                    <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Status</th>
                    <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Joined</th>
                    <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Actions</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {filteredUsers.map((user) => (
                    <tr key={user.id} className="hover:bg-gray-50 transition-colors">
                      <td className="px-6 py-4">
                        <div className="flex items-center space-x-4">
                          <div className="w-12 h-12 bg-blue-600 rounded-xl flex items-center justify-center text-white text-sm font-bold shadow-md">
                            {user.name?.charAt(0) || 'U'}
                          </div>
                          <div>
                            <p className="text-gray-900 font-semibold text-base">{user.name || 'No Name'}</p>
                            <p className="text-gray-500 text-sm">{user.email}</p>
                            {user.phone && <p className="text-gray-500 text-sm">{user.phone}</p>}
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4">
                        <span className={`px-3 py-1.5 rounded-full text-xs font-semibold ${getRoleBadgeColor(user.user_type)}`}>
                          {user.user_type || 'NORMAL'}
                        </span>
                      </td>
                      <td className="px-6 py-4">
                        <span className={`px-3 py-1.5 rounded-full text-xs font-semibold ${getStatusBadgeColor(user.status)}`}>
                          {user.status || 'ACTIVE'}
                        </span>
                      </td>
                      <td className="px-6 py-4 text-gray-600 text-sm">
                        {user.created_at ? new Date(user.created_at).toLocaleDateString() : 'N/A'}
                      </td>
                      <td className="px-6 py-4">
                        <div className="flex items-center space-x-2">
                          <button
                            onClick={() => openUserModal(user)}
                            className="bg-blue-600 text-white px-3 py-1.5 rounded-lg hover:bg-blue-700 transition-colors text-sm font-medium"
                          >
                            View
                          </button>
                          {user.status !== 'ADMIN' && user.status !== 'DELETED' && (
                            user.status === 'ACTIVE' ? (
                              <button
                                onClick={() => handleUserAction(user.id, 'SUSPENDED')}
                                disabled={actionLoading === user.id}
                                className="bg-yellow-600 text-white px-3 py-1.5 rounded-lg hover:bg-yellow-700 transition-colors text-sm font-medium disabled:opacity-50"
                              >
                                {actionLoading === user.id ? '...' : 'Suspend'}
                              </button>
                            ) : user.status === 'SUSPENDED' ? (
                              <button
                                onClick={() => handleUserAction(user.id, 'ACTIVE')}
                                disabled={actionLoading === user.id}
                                className="bg-green-600 text-white px-3 py-1.5 rounded-lg hover:bg-green-700 transition-colors text-sm font-medium disabled:opacity-50"
                              >
                                {actionLoading === user.id ? '...' : 'Activate'}
                              </button>
                            ) : null
                          )}
                          {user.status !== 'DELETED' && (
                            <button
                              onClick={() => handleDeleteUser(user.id)}
                              disabled={actionLoading === user.id}
                              className="bg-red-600 text-white px-3 py-1.5 rounded-lg hover:bg-red-700 transition-colors text-sm font-medium disabled:opacity-50"
                            >
                              {actionLoading === user.id ? '...' : 'Delete'}
                            </button>
                          )}
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>

      {/* User Details Modal */}
      {showUserModal && selectedUser && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowUserModal(false)}></div>
            
            <div className="inline-block align-bottom bg-white rounded-2xl text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-4xl sm:w-full">
              <div className="bg-white px-6 py-8">
                <div className="flex items-center justify-between mb-6">
                  <h3 className="text-2xl font-bold text-gray-900">User Details</h3>
                  <button
                    onClick={() => setShowUserModal(false)}
                    className="text-gray-400 hover:text-gray-600"
                  >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>

                <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                  {/* Profile Information */}
                  <div className="space-y-6">
                    <div>
                      <h4 className="text-lg font-semibold text-gray-900 mb-4">Profile Information</h4>
                      <div className="space-y-3">
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Name</label>
                          <p className="text-gray-900">{selectedUser.name || 'Not provided'}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Email</label>
                          <p className="text-gray-900">{selectedUser.email}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Phone</label>
                          <p className="text-gray-900">{selectedUser.phone || 'Not provided'}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">User Type</label>
                          <span className={`inline-block px-3 py-1.5 rounded-full text-xs font-semibold ${getRoleBadgeColor(selectedUser.user_type)}`}>
                            {selectedUser.user_type || 'NORMAL'}
                          </span>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Status</label>
                          <span className={`inline-block px-3 py-1.5 rounded-full text-xs font-semibold ${getStatusBadgeColor(selectedUser.status)}`}>
                            {selectedUser.status || 'ACTIVE'}
                          </span>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Joined</label>
                          <p className="text-gray-900">{selectedUser.created_at ? new Date(selectedUser.created_at).toLocaleString() : 'N/A'}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-2">Subscription Status</label>
                          <div className="flex items-center gap-3">
                            <span className={`px-3 py-1.5 rounded-full text-xs font-semibold ${
                              selectedUser.is_subscribed 
                                ? 'bg-green-100 text-green-800' 
                                : 'bg-gray-100 text-gray-800'
                            }`}>
                              {selectedUser.is_subscribed ? 'Subscribed' : 'Not Subscribed'}
                            </span>
                            {selectedUser.subscription_expiration && (
                              <span className="text-sm text-gray-600 flex items-center gap-1">
                                <Calendar className="w-4 h-4" />
                                Expires: {new Date(selectedUser.subscription_expiration).toLocaleDateString()}
                              </span>
                            )}
                          </div>
                          <div className="mt-2 flex items-center gap-2">
                            <button
                              onClick={openSubscriptionModal}
                              className="bg-purple-600 text-white px-4 py-2 rounded-lg hover:bg-purple-700 transition-colors text-sm font-medium flex items-center gap-2"
                            >
                              <Crown className="w-4 h-4" />
                              {selectedUser.is_subscribed ? 'Change Subscription' : 'Assign Subscription'}
                            </button>
                            {selectedUser.is_subscribed && (
                              <button
                                onClick={handleRemoveSubscription}
                                disabled={assigningSubscription}
                                className="bg-red-600 text-white px-4 py-2 rounded-lg hover:bg-red-700 transition-colors text-sm font-medium flex items-center gap-2 disabled:opacity-50 disabled:cursor-not-allowed"
                              >
                                <Trash2 className="w-4 h-4" />
                                {assigningSubscription ? 'Removing...' : 'Remove Subscription'}
                              </button>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  {/* Statistics */}
                  <div className="space-y-6">
                    <div>
                      <h4 className="text-lg font-semibold text-gray-900 mb-4">Account Statistics</h4>
                      <div className="grid grid-cols-2 gap-4">
                        <div className="bg-blue-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-blue-600">{selectedUser.total_uploads || 0}</p>
                          <p className="text-blue-700 text-sm font-medium">Total Uploads</p>
                        </div>
                        <div className="bg-green-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-green-600">{selectedUser.total_likes || 0}</p>
                          <p className="text-green-700 text-sm font-medium">Total Likes</p>
                        </div>
                        <div className="bg-purple-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-purple-600">{selectedUser.is_subscribed ? 'Yes' : 'No'}</p>
                          <p className="text-purple-700 text-sm font-medium">Subscribed</p>
                        </div>
                        <div className="bg-yellow-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-yellow-600">{selectedUser.total_views || 0}</p>
                          <p className="text-yellow-700 text-sm font-medium">Total Views</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Action Buttons */}
                <div className="flex items-center justify-end space-x-4 mt-8 pt-6 border-t border-gray-200">
                  {selectedUser.status !== 'ADMIN' && selectedUser.status !== 'DELETED' && (
                    selectedUser.status === 'ACTIVE' ? (
                      <button
                        onClick={() => handleUserAction(selectedUser.id, 'SUSPENDED')}
                        disabled={actionLoading === selectedUser.id}
                        className="bg-yellow-600 text-white px-6 py-3 rounded-xl hover:bg-yellow-700 transition-colors font-semibold disabled:opacity-50"
                      >
                        {actionLoading === selectedUser.id ? 'Processing...' : 'Suspend User'}
                      </button>
                    ) : selectedUser.status === 'SUSPENDED' ? (
                      <button
                        onClick={() => handleUserAction(selectedUser.id, 'ACTIVE')}
                        disabled={actionLoading === selectedUser.id}
                        className="bg-green-600 text-white px-6 py-3 rounded-xl hover:bg-green-700 transition-colors font-semibold disabled:opacity-50"
                      >
                        {actionLoading === selectedUser.id ? 'Processing...' : 'Activate User'}
                      </button>
                    ) : null
                  )}
                  {selectedUser.status !== 'DELETED' && (
                    <button
                      onClick={() => handleDeleteUser(selectedUser.id)}
                      disabled={actionLoading === selectedUser.id}
                      className="bg-red-600 text-white px-6 py-3 rounded-xl hover:bg-red-700 transition-colors font-semibold disabled:opacity-50"
                    >
                      {actionLoading === selectedUser.id ? 'Processing...' : 'Delete User'}
                    </button>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Subscription Assignment Modal */}
      {showSubscriptionModal && selectedUser && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowSubscriptionModal(false)}></div>
            
            <div className="inline-block align-bottom bg-white rounded-2xl text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-2xl sm:w-full">
              <div className="bg-white px-6 py-8">
                <div className="flex items-center justify-between mb-6">
                  <div>
                    <h3 className="text-2xl font-bold text-gray-900">Assign Subscription</h3>
                    <p className="text-sm text-gray-600 mt-1">User: {selectedUser.email}</p>
                  </div>
                  <button
                    onClick={() => setShowSubscriptionModal(false)}
                    className="text-gray-400 hover:text-gray-600"
                  >
                    <X className="w-6 h-6" />
                  </button>
                </div>

                {loadingPlans ? (
                  <div className="text-center py-8">
                    <div className="w-12 h-12 border-4 border-gray-200 border-t-blue-600 rounded-full animate-spin mx-auto mb-4"></div>
                    <p className="text-gray-600">Loading subscription plans...</p>
                  </div>
                ) : subscriptionPlans.length === 0 ? (
                  <div className="text-center py-8">
                    <p className="text-gray-600">No subscription plans available</p>
                  </div>
                ) : (
                  <div className="space-y-4">
                    <div>
                      <label className="block text-sm font-semibold text-gray-700 mb-3">Select Subscription Plan</label>
                      <div className="space-y-2 max-h-96 overflow-y-auto">
                        {subscriptionPlans.map((plan) => (
                          <div
                            key={plan.plan_id}
                            onClick={() => setSelectedPlanId(plan.plan_id)}
                            className={`p-4 border-2 rounded-xl cursor-pointer transition-all ${
                              selectedPlanId === plan.plan_id
                                ? 'border-purple-600 bg-purple-50'
                                : 'border-gray-200 hover:border-gray-300'
                            }`}
                          >
                            <div className="flex items-center justify-between">
                              <div className="flex-1">
                                <div className="flex items-center gap-2 mb-1">
                                  <h4 className="font-semibold text-gray-900">{plan.plan_name}</h4>
                                  <span className={`px-2 py-0.5 rounded text-xs font-medium ${
                                    plan.plan_type === 'YEARLY' ? 'bg-green-100 text-green-800' :
                                    plan.plan_type === 'MONTHLY' ? 'bg-blue-100 text-blue-800' :
                                    'bg-purple-100 text-purple-800'
                                  }`}>
                                    {plan.plan_type}
                                  </span>
                                </div>
                                {plan.plan_description && (
                                  <p className="text-sm text-gray-600 mb-2">{plan.plan_description}</p>
                                )}
                                <div className="flex items-center gap-4 text-sm text-gray-600">
                                  <span className="font-semibold text-gray-900">${parseFloat(plan.price).toFixed(2)}</span>
                                  <span>{plan.duration_days} days</span>
                                </div>
                              </div>
                              <div className={`w-5 h-5 rounded-full border-2 flex items-center justify-center ${
                                selectedPlanId === plan.plan_id
                                  ? 'border-purple-600 bg-purple-600'
                                  : 'border-gray-300'
                              }`}>
                                {selectedPlanId === plan.plan_id && (
                                  <div className="w-3 h-3 rounded-full bg-white"></div>
                                )}
                              </div>
                            </div>
                          </div>
                        ))}
                      </div>
                    </div>
                  </div>
                )}

                {/* Action Buttons */}
                <div className="flex items-center justify-end space-x-4 mt-8 pt-6 border-t border-gray-200">
                  <button
                    onClick={() => setShowSubscriptionModal(false)}
                    className="px-6 py-3 rounded-xl border border-gray-300 text-gray-700 hover:bg-gray-50 transition-colors font-semibold"
                  >
                    Cancel
                  </button>
                  <button
                    onClick={handleAssignSubscription}
                    disabled={!selectedPlanId || assigningSubscription}
                    className="px-6 py-3 rounded-xl bg-purple-600 text-white hover:bg-purple-700 transition-colors font-semibold disabled:opacity-50 disabled:cursor-not-allowed flex items-center gap-2"
                  >
                    {assigningSubscription ? (
                      <>
                        <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                        Assigning...
                      </>
                    ) : (
                      <>
                        <Crown className="w-4 h-4" />
                        Assign Subscription
                      </>
                    )}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default UserManagement;
